<template>
  <div style="right:0px;top:1.35rem;"
       class="position-absolute bg-light w-100">
    <ul class="list-group text-center">
      <li class="list-group-item p-1 cursor-pointer"
          @click="editItem">{{$t('edit')}}</li>
      <li class="list-group-item p-1 cursor-pointer"
          @click="deleteItem">{{$t('delete')}}</li>
    </ul>
  </div>
</template>
<script>
export default {
  data () { return {} },
  methods: {
    deleteItem () {
      this.$emit('delete')
    },
    editItem () {
      this.$emit('edit')
    }
  }
}
</script>



