<template>
  <div class="modal fade"
       id="navigatorAppListAddModal"
       tabindex="-1"
       role="dialog"
       aria-labelledby="exampleModalScrollableTitle"
       aria-hidden="true">
    <div class="modal-dialog modal-dialog-scrollable"
         role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title"
              id="exampleModalScrollableTitle">{{$t('add')}}</h5>
          <button type="button"
                  class="close"
                  data-dismiss="modal"
                  aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="alert alert-info">{{$t('appLinkEditTips')}}</div>
        <div class="modal-body">
          <form>
            <div class="form-group row">
              <label class="col-3 justify-content-start">{{$t('linkName')}}</label>
              <div class="col-9">
                <input type="text"
                       style="width: 100%;"
                       class="form-control"
                       :placeholder="$t('linkName')"
                       v-model="link.name">
              </div>
            </div>
            <div class="form-group row">
              <label class="col-3 justify-content-start">{{$t('linkAddress')}}</label>
              <div class="col-9">
                <input type="text"
                       style="display: block;width: 100%;"
                       class="form-control"
                       :placeholder="$t('linkSample')"
                       v-model="link.url">
              </div>
            </div>
            <div class="form-group row">
              <label class="col-3 justify-content-start">{{$t('linkIcon')}}</label>
              <div class="col-9">
                <input type="text"
                       style="display: block;width: 100%;"
                       class="form-control"
                       :placeholder="$t('linkIcon')"
                       v-model="link.icon">
              </div>
            </div>
          </form>
        </div>
        <div class="modal-footer">
          <button type="button"
                  class="btn btn-secondary"
                  data-dismiss="modal"
                  @click="toggle">{{$t('cancel')}}</button>
          <button type="button"
                  class="btn btn-primary"
                  @click="save">{{$t('save')}}</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () { return { link: {}, currentEditType: '' } },
  methods: {
    updateToggle (id) {
      this.currentEditType = 'u'
      var linkArr = this.getLocalLinks()
      this.link = linkArr.filter(item => { return item.id === id })[0]
      this.toggle()
    },
    toggle () {
      $('#navigatorAppListAddModal').modal('toggle')
    },
    save () {
      if (this.currentEditType === 'u') {
        this.update()
      } else {
        this.add()
      }
    },
    update () {
      var linkArr = this.getLocalLinks()
      var vueObj = this
      linkArr.forEach((item, index) => {
        if (item.id === vueObj.link.id) {
          linkArr[index] = vueObj.link
          var newLinkAtr = JSON.stringify(linkArr)
          localStorage.setItem('links', newLinkAtr)
          vueObj.toggle()
          vueObj.$emit('linkUpdated')
          vueObj.reset()
        }
      })
    },
    add () {
      var linkArr = this.getLocalLinks()
      this.link.id = linkArr.length + 1
      linkArr.push(this.link)
      var newLinkAtr = JSON.stringify(linkArr)
      localStorage.setItem('links', newLinkAtr)
      this.toggle()
      this.$emit('linkAdded')
      this.reset()
    },
    getLocalLinks () {
      var linkStr = localStorage.getItem('links') === null ? '[]' : localStorage.getItem('links')
      return JSON.parse(linkStr)
    },
    reset () {
      this.link = {}
      this.currentEditType = ''
    }
  }
}
</script>



