<template>
  <div>
    <ul class="list-inline position-absolute bg-white p-3 border animated faster shadow"
        id="my-site-app-list"
        style="margin-left:-17.3rem;margin-top:1.5rem;font-size:13px;width:17.2rem;z-index:16;">
      <li class="list-inline-item float-left text-truncate mr-0"
          style="width:5rem;height:6rem;"
          v-for="(item,index) in links"
          :key="index">
        <img :src="item.icon"
             class="mx-auto d-block app-icon rounded shadow" />
        <div class="text-truncate text-center">
          <a :href="item.url"
             :title="item.name">{{item.name}}</a>
        </div>
      </li>
      <li class="list-inline-item float-left text-truncate mr-0 position-relative"
          style="width:5rem;height:6rem;"
          v-for="(item,index) in localLinks"
          :key="index+links.length">
        <div class="app-icon-container">
          <div class="shadow-sm rounded-circle iconfont icon-caozuo position-absolute bg-light text-center shadow app-icon-operation"
               @click="toggleOperation(index)"></div>
          <navigator-app-list-app-operation v-show="item.operationShow"
                                            @delete="deleteLink(item.id)"
                                            @edit="editLink(item.id)"></navigator-app-list-app-operation>
          <img :src="item.icon"
               class="mx-auto d-block app-icon rounded shadow" />
        </div>
        <div class="text-truncate text-center">
          <a :href="item.url"
             :title="item.name"
             target="_blank">{{item.name}}</a>
        </div>
      </li>
      <li class="list-inline-item float-left text-truncate "
          style="width:5rem;height:6rem;">
        <div class="mx-auto d-block app-icon rounded">
          <button class="btn btn-outline-dark border-0 iconfont icon-tianjia w-100"
                  style="font-size:2rem;"
                  @click="addLink"></button>
        </div>
      </li>
    </ul>
    <navigator-app-list-add-modal ref="navigatorAppListAddModal"
                                  @linkAdded="reload"
                                  @linkUpdated="reload"></navigator-app-list-add-modal>
  </div>
</template>
<script>
import NavigatorAppListAddModal from './NavigatorAppListAddModal'
import NavigatorAppListAppOperation from './NavigatorAppListAppOperation'
export default {
  data () {
    return { links: [], localLinks: [] }
  },
  components: { NavigatorAppListAddModal, NavigatorAppListAppOperation },
  created () {
    this.reload()
  },
  methods: {
    reload () {
      this.$http.get('/service/link').then(res => {
        return res.json()
      }).then(data => {
        this.links = data
      })
      var linkStr = localStorage.getItem('links') === null ? '[]' : localStorage.getItem('links')
      this.localLinks = JSON.parse(linkStr)
    },
    addLink () {
      this.$refs.navigatorAppListAddModal.toggle()
    },
    toggleOperation (index) {
      this.localLinks[index].operationShow = !this.localLinks[index].operationShow
      this.$set(this.localLinks, index, this.localLinks[index])
    },
    deleteLink (id) {
      this.localLinks.forEach((item, index) => {
        if (item.id === id) {
          this.localLinks.splice(index, 1)
          localStorage.setItem('links', JSON.stringify(this.localLinks))
          this.reload()
        }
      })
    },
    editLink (id) {
      this.$refs.navigatorAppListAddModal.updateToggle(id)
    }
  }
}
</script>
<style scoped>
#my-site-app-list::before {
  content: "";
  margin-top: -2.4rem;
  position: absolute;
  right: 1rem;
  border: solid;
  border-color: rgba(255, 255, 255, 0);
  border-bottom-color: #dee2e6;
  border-width: 0.7rem;
}
.app-icon-container:hover .app-icon-operation {
  display: block;
}
.app-icon {
  border: #dee2e6 solid 1px !important ;
  width: 4rem;
}
.app-icon-operation {
  right: 0rem;
  top: 0rem;
  width: 2rem;
  display: none;
}
.app-icon-operation:hover {
  background-color: #cecece !important;
}
</style>



