import messageVue from './messages.vue'

// å®šä¹‰æ’ä»¶å¯¹è±¡
const MessageBox = {}
// vueçš„installæ–¹æ³•ï¼Œç”¨äºŽå®šä¹‰vueæ’ä»¶
MessageBox.install = function (Vue, options) {
  const MessageBoxInstance = Vue.extend(messageVue)
  let currentMsg
  const initInstance = () => {
    // å®žä¾‹åŒ–vueå®žä¾‹
    currentMsg = new MessageBoxInstance()
    let msgBoxEl = currentMsg.$mount().$el
    document.body.appendChild(msgBoxEl)
  }
  // åœ¨Vueçš„åŽŸåž‹ä¸Šæ·»åŠ å®žä¾‹æ–¹æ³•ï¼Œä»¥å…¨å±€è°ƒç”¨
  Vue.prototype.$msgBox = {
    toggle () {
      if (!currentMsg) {
        initInstance()
      }
      currentMsg.toggle()
    },
    newMessage (messageParam) {
      if (!currentMsg) {
        initInstance()
      }
      currentMsg.newMessage(messageParam)
    }
  }
}
export default MessageBox



