<template>
  <div class="col-12 p-2 bg-white mt-4 text-center"
       style="height:5rem;">
    <div>{{$t('copyrightStatement')}}</div>
    <div v-if="siteMode==='github'">{{$t('poweredLabelGithub')}}</div>
    <div v-if="siteMode==='gitee'">{{$t('poweredLabelGitee')}}</div>
    <div v-if="siteMode==='localhost'">{{$t('localDevMode')}}</div>
    <div v-if="siteMode==='unknow'">{{$t('unknowMode')}}</div>
    <div>
      <span v-if="siteMode==='github'">{{$t('internationalSite')+'/'}}</span>
      <span v-if="siteMode==='gitee'">{{$t('chinaMainlandSite')+'/'}}</span>
      <a v-if="siteMode==='github'"
         :href="anotherSiteUrl">{{$t('chinaMainlandSite')}}</a>
      <a v-if="siteMode==='gitee'"
         :href="anotherSiteUrl">{{$t('internationalSite')}}</a>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return { poweredLabel: '', anotherSiteUrl: '', siteMode: '' }
  },
  mounted () {
    var domain = document.domain
    if (domain === 'lucouse.github.io') {
      this.siteMode = 'github'
      this.anotherSiteUrl = 'http://lucouse.gitee.io/my-site'
    } else if (domain === 'lucouse.gitee.io') {
      this.siteMode = 'gitee'
      this.anotherSiteUrl = 'http://lucouse.github.io/my-site'
    } else if (domain === 'localhost') {
      this.siteMode = 'localhost'
    } else {
      this.siteMode = 'unknow'
    }
  }
}
</script>



